.card {
  background: #fff;
  border: 5px solid #f1f1f1;
  border-radius: 3px;
  height: 225px;
  margin: 1rem;
  position: relative;
  width: 225px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-transition: .1s;
  -o-transition: .1s;
  transition: .1s;
  -webkit-box-shadow: 0 14px 28px #00000040, 0 10px 10px #00000038;
          box-shadow: 0 14px 28px #00000040, 0 10px 10px #00000038;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
}
.card > .img-container > img {
  width: 60%
}

